var React = require('react');
var ReactDOM = require('react-dom');
var ButtonConfirm = require('../../../component/form/button/button-confirm');
var ButtonExample = React.createClass({
  getInitialState() {
    return {
      msg: ''
    }
  },
  onYes(title) {
    this.setState({
      msg: '' + title
    });
  },
  onNo(title) {
    this.setState({
      msg: '' + title
    });
  },
  render() {
    var { msg } = this.state;
    return (<div ref="">
      <div className="panel panel-default">
        <div className="panel-heading"></div>
        <div className="panel-body">
          <ButtonConfirm onYes={() => {this.onYes('')}} onNo={() => {this.onNo('')}} title="" />
          <div style={{marginBottom: '10px'}}></div>
          <ButtonConfirm onYes={() => {this.onYes('')}} onNo={() => {this.onNo('')}} title="" />
          <div style={{marginBottom: '10px'}}></div>
          <ButtonConfirm onYes={() => {this.onYes('')}} onNo={() => {this.onNo('')}} title="" />
          <div style={{marginBottom: '10px'}}></div>
          <span style={{marginTop: '10px', 'display': 'block'}}>{msg}</span>
        </div>
      </div>

    </div>);
  }
});

module.exports = ButtonExample;


