var React = require('react');
var Formsy = require('formsy-react');
var _ = require('underscore');
var InputBase = require('../../../component/form/Form-item-base');
var InputAsyncValid = require('../../../component/form/input-async-valid');
require('../../../component/form/form.less');

var FormExample = React.createClass({
  getInitialState() {
    return {
      validateRepeat: null
    }
  },
  submit(model, reset, invalidate) {
  },
  onValidSubmit(model) {
    alert('');
  },
  asyncValidate(value, isValid) {
  },
  render() {
    return (<div style={{marginTop: '30px'}}>
      <Formsy.Form onSubmit={this.submit} ref="form" onValidSubmit={this.onValidSubmit}>
        <div className="Form-item clearfix">
          <label></label>
          <InputAsyncValid
            name="email" validations="isEmail" validationError=""
            required tips=",xxx@qq.com" placeholder=""/>
        </div>
        <div className="Form-item clearfix">
          <label></label>
          <InputBase name="num1" validations="isNumeric" validationError="" required tips=""  placeholder=""/>
        </div>
        <div className="Form-item clearfix">
          <label></label>
          <InputBase name="word" validations="isWords" validationError="" required tips="" placeholder=""/>
        </div>
        <div className="Form-item clearfix">
          <label></label>
          <InputBase name="pwd" validations="isNumeric" validationError="" required tips=""  placeholder=""/>
        </div>
        <div className="Form-item clearfix">
          <label></label>
          <InputBase name="pwdRepeat" validations={{
            ifSameValid: function(values, value) {
              if (values.pwd != value) {
                return '';
              }
              return true;
            }
          }} required tips=""  placeholder=""/>
        </div>
        <button type="submit" className="btn btn-primary" style={{marginLeft: '140px'}}></button>
      </Formsy.Form>
    </div>);
  }
});

module.exports = FormExample;


